html,
body {
        height: 100%;
        margin: 0;
        padding: 0;
}

map {
        height: 50%;
        width: 50%;
        display: none;
}

main-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    color: white;
}

button {
    z-index: 1;
    background-color: silver;
    width: 200px;
    height: 50px;
    border-radius: 20px;
    font-size: 24px;
    color: white;
}
